.container{
    width: 80%;
    margin: 3.125rem auto;
    .grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 1rem;
        .gridItems{
            opacity: .8;
            transform: scale(110%);
            
            margin-bottom: 3.25rem;
            .title{
                text-align: center;
            }
        }
        .gridItems:hover{
            opacity: 1;
            cursor: pointer;
        }
        .moviePoster{
            border-radius: 0.625rem;
            height: 22.6875rem;
            width: 15rem;
        }
    }
    .noImg {
        border: 1px solid var(--gray);
        border-radius: 0.625rem;
        height: 22.6875rem;
        width: 15rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .seeMore{
        position: absolute;
        bottom: 3.125rem;
        left: 1.25rem;
        background-color: var(--yellow);
        padding: 0.625rem 3.125rem 0.625rem 1.25rem;
        color: var(--brown);
        opacity: .6;
        .chev{
            width: 18%;
            position: absolute;
            top: .5rem;
            right: 0.625rem;
        }
    }
    .seeMore:hover{
        opacity: 1;
        transform: scale(110%);
    }
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
    0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    }
    100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    }
}
@keyframes scale-up-center {
    0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    }
    100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    }
}