:root{
    --black : #04040C;
    --blue: #041623;
    --gray: #AABFC0;
    --brown: #444C4F;
    --yellow: #C2BC4F;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--gray);
    font-family: 'Barlow', monospace;
    transition: all .2s ease-in-out;
}
body{
    background-color: var(--black);
}
::-webkit-scrollbar {
    width: 0;
}
a, a:visited {
    text-decoration: none;
    color: black;
}
