.gradient {
    margin-top: 0.625rem;
    width: 100vw;
    background: linear-gradient(rgba(0, 0, 0, 0), rgb(0, 0, 0));
    padding-bottom: 56.25%;
}
.backdrop {
    position: absolute;
    top: 5.525rem;
    width: 100%;
    z-index: -1;
}
.movieContainer{
    position: absolute;
    top: 28.125rem;
    left: 1.25rem;
    width: 60%;
    .mainTitle {
        position: relative;
        left: -5rem;
        top: -4.75rem;
    }
    .overview{
        position: relative;
        left: 1.25rem;
        top: 1.875rem;
        
    }
    .release{

        position: relative;
        left: 1.25rem;
        top: 3rem;
    }
    .status{

        position: relative;
        left: 1.25rem;
        top: 3.125rem;
    }
    .genres{
        position: relative;
        left: 1.25rem;
        top: 3rem;

    }
}
.btn {
    position: absolute;
    background-color: var(--yellow);
    padding: 0.625rem 1.25rem 0.625rem 1.25rem;
    color: var(--brown);
}
.btn:hover {
    cursor: pointer;
    transform: scale(110%);
}
.trailer{
    top: 30rem;
    right: 12.5rem;
}
.back {
    top: 9.375rem;
    left: 3.75rem;
    background-color: transparent;
    border-radius: 50%;
}
.home {
    top: 9.375rem;
    right: 3.75rem;
    background-color: transparent;
    border-radius: 50%;
}
.no-img {
    font-size: xx-large;
    height: 60vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.close{
    position: absolute;
    z-index: 2000;
    top: 100px;
    right: 100px;
    // opacity: .2;
    :hover {
        border-radius: 5px;
        background-color: red;
        transform: scale(110%);
        opacity: 1;
    }
}
.wrapper {
    overflow: hidden;
    max-width: 100%;
    .youtube-container{
        z-index: 1;
        position: relative;
    padding-bottom: 56.25%; /* 16:9 */  
    padding-top: 25px;
    width: 300%; /* enlarge beyond browser width */
    left: -100%; /* center */
        .trailer {
            position: absolute; 
            top: 0; 
            left: 0; 
            width: 100%; 
            height: 100%;
        }
}
}
