.recommendation {
    transition: all .3s ease;
    margin: 5rem 0.625rem;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
    .noRecomm{
        position: relative;
        margin-bottom: 1.25rem;
        left: 6.25rem;
    }
    .RecomTitle{
        position: absolute;
        bottom: 4.375rem;
        left: 1.25rem;
        font-size: larger;
    }
    }
    .gridItems{
        opacity: .8;
        margin-bottom: 1.25rem;
        .title{
            text-align: center;
        }
    }
    .gridItems:hover{
        opacity: 1;
        cursor: pointer;
    }
    .moviePoster{
        border-radius: 0.625rem;
        height: 22.6875;
        width: 15rem;
    }