.navbar{
    background-color: var(--yellow);
    height: 5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    .logo {
        img {
            height: 3.125rem;
            border-radius: 100%;
        }
    }
    .searchBar{
        display: flex;
        justify-content: center;
        .search {
            outline: none;
            border: none;
            background-color: #f8f8f8;
            color: var(--brown);
            padding: 0.5rem 0.5rem;
            width: 15.625rem;
        }
    }
    .searchResults{
        position: absolute;
        z-index: 3000;
        top: 55px;
        right: 245px;
        overflow: scroll;
        display: grid;
        text-align: center;
        grid-template-columns: 15.625rem;
        max-height: 500px;
        .searchItems:not(:first-child) {
            border-top: 1px solid var(--brown);
        }
        .searchItems{
            background-color: var(--black);
            cursor: pointer;
            display: grid;
            text-align: center;
            grid-template-columns: 1fr 1fr;
            min-height: 4.125rem;
            max-height: 158px;
            padding: 0.5rem 0.5rem;
            .title{
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
}
h1{
    font-weight: 700;
    font-size: 2.25rem;
    margin: 1.25rem 0 0 6.35rem;
}